@media screen and (min-width: 60em){
  .temporary-l{
    display: grid !important;
    grid-auto-flow: row;
    grid-template: repeat(auto-fill, minmax(200px,1fr)) /1fr 1fr 1fr;
    align-content: space-evenly;
  }
  .grid-column-start-2{
    grid-column-start: 2;
  }
  .image{
    height: 150px;
    width: 150px;
  }


}

@media screen and (max-width: 59em){
  .temporary{
    display: grid !important;
    grid-auto-flow: row;
    grid-template: repeat(auto-fill, minmax(200px,1fr)) /minmax(80px, 100px) 1fr;
    align-content: space-evenly;
    grid-gap: 20px
  }
  .image{
    height: 100px;
    width: 100px;
  }
}



